import React, {Component} from 'react';
import {Button} from 'react-bootstrap';
import './socialmedia.css';


class Socialmedia extends Component {
  render(){
    return(
      <div>
          {/*linkedin*/}
          <div className = "my-links">
            <a href ="https://www.linkedin.com/in/shekhar-chaudhary-b5a759148/" rel="noopener noreferrer" target ="_blank">
              <Button bsStyle="primary">linkedin</Button>
            </a>
          </div>
          {/*github*/}
          <div className = "my-links">
            <a href ="https://github.com/shekharchaudhary" rel="noopener noreferrer" target ="_blank">
              <Button bsStyle="primary">github</Button>
            </a>
          </div>
          {/*codepen*/}
          <div className = "my-links">
            <a href ="https://codepen.io/shikharkazi/" rel="noopener noreferrer" target ="_blank">
              <Button bsStyle="primary">codepen</Button>
            </a>
          </div>
      </div>
    );
  }
}
export default Socialmedia;



