import React ,{Component} from 'react';
import Fulldate from './date'
import './blog.css';

export default class Blog extends Component {
  render(){
    return(
      <div className='hey' style={{background:'url(https://images.unsplash.com/photo-1518775005910-7aa25aa9614a?ixlib=rb-0.3.5&q=85&fm=jpg&crop=entropy&cs=srgb&ixid=eyJhcHBfaWQiOjE0NTg5fQ&s=47962c50d60b152690f172b48d024c8c) center / cover'}}>
        <div className="blog-card spring-fever">
          <div className="title-content">
          <h3><a href="">This is my inspiring Blog</a></h3>
          <div className="intro"> <a href="/">Inspiration</a> </div>
          </div>
          <div className="card-info">
            This is my Inspiration
          <a href="/">Read Article<span class="licon icon-arr icon-black"></span></a>
          </div>
          <div className="utility-info">
          <ul className="utility-list">
            <li><span className="licon icon-like"></span><a href="/">PHOTOS</a></li>
            <li><span className="licon icon-com"></span><a href="/">VIDEOS</a></li>
            <Fulldate />
          </ul>
          </div>
          <div className="gradient-overlay"></div>
          <div className="color-overlay"></div>
        </div>
      </div>
    );
  }
}



