import React, { Component} from 'react'
import {Grid, Cell} from 'react-mdl';
import Socialmedia from './socialmedia';
import './home.css'


export default class Contact  extends Component {
  render(){
    return(
      <div className='hey' style={{background:'url(https://images.unsplash.com/photo-1487581424120-18f3611a63e6?ixlib=rb-0.3.5&q=85&fm=jpg&crop=entropy&cs=srgb&ixid=eyJhcHBfaWQiOjE0NTg5fQ&s=7ea2697958721e06a769825436f581a3) center / cover'}}>
        <div className='wrapper'>
          <Grid className='contact-grid'>
            <Cell col={12}>
              <h2>contact<span style={{color:'#2bb3c0'}}> me</span></h2>
              <hr/>
              <p>
              Pinole, California, 94564<br></br> Email: webdevshekhar@gmail.com <br></br> Phone: (510)-417 7347
              </p>
            </Cell>
            <Socialmedia
            />
          </Grid>
      </div>
    </div>
    );
  }
}



